import React, { useRef } from 'react';

export default function MemberList({ memberList, offerToMember }) {
    
    const audioRef = useRef();

    function addStream(stream) {
        if (stream) {
            audioRef.current.srcObject = stream;
            audioRef.current.autoplay = 'autoplay';
        }
    }

    return <ul>
        {memberList.map(member =>
            (<li key={member.key} onDoubleClick={() => { offerToMember(member) } }>
                <p>{member.id}</p>
                <p>{member.status}</p>
                <p>{member.name}</p>
                <audio ref={audioRef} onClick={addStream(member.stream)}></audio>
            </li>))}
    </ul>
}