import { Avatar, Button, ButtonGroup, Card, CardContent, CardHeader, Chip, makeStyles, Typography } from '@material-ui/core';
import { deepOrange } from '@material-ui/core/colors';
import React from 'react';

const useStyles = makeStyles((theme) => ({
    orange: {
        color: theme.palette.getContrastText(deepOrange[500]),
        backgroundColor: deepOrange[500],
    },
    cardHeader: {
        alignItems: "center"
    },
    cardContent: {
        display: "flex",
        justifyContent: "space-between"
    },
    cardStatusChip: {
        alignSelf: "center"
    },
    statusButton: {

    },
    workingStatusButton: {

    },
    restStatusButton: {

    },
    offStatusButton: {

    },
    selectedButton: {

    }
}));

export default function MyInfoBox({myInfo}) {
    const styles = useStyles();

    console.log(myInfo);
    return (
        <Card elevation={0}>
            <CardHeader
                className={styles.cardHeader}
                avatar={<Avatar className={styles.orange}>응</Avatar>}
                title={myInfo.name}
                action={<Chip className={styles.cardStatusChip} color="primary" label="업무중" />}
            >
            </CardHeader>
            <CardContent className={styles.cardContent}>
                <Button className={styles.workingStatusButton}>업무중</Button>
                <Button className={styles.workingStatusButton}>회의</Button>
                <Button className={styles.restStatusButton}>휴식</Button>
                <Button className={styles.restStatusButton}>식사</Button>
                <Button className={styles.offStatusButton}>퇴근</Button>
            </CardContent>

        </Card>
    )
}